@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

body {
    font-family: 'Lato';
    --services: slategray;
}

h2 {
    margin-bottom: 1em;
}

/* LANDING PAGE - MOBILE & DESKTOP */

.sectionOneText p, .sectionOneTextMobile p, .sectionTwoText p, .sectionTwoTextMobile p, .sectionThreeText p, .sectionThreeTextMobile p {
    font-size: 90%;
}

.sectionOneContainer {
    display: flex;
    justify-content: center;
    line-height: 170%;
}

.sectionOneText {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 60%;
}

.sectionOneTextMobile {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 100%;
}

.sectionTwoText {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 50%;
}

.sectionTwoTextMobile {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 100%;
    text-align: center;
}

.sectionTwoFiller {
    display: flex;
    justify-content: center;
    background-color: white;
    line-height: 170%;
    width: 50%;
}

.sectionThreeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5%;
    background-color: #ABB7B7;
    line-height: 170%;
    max-width: 100%;
}

.sectionThreeText {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 60%;
    margin-top: 1em;
}

.sectionThreeTextMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    margin-top: 1em;
}

.image {
    object-fit: cover;
    width: 40%;
}
.portrait {
    object-fit: cover;
    width: 20%;
    border-radius: 100%;
    margin-top: 1em;
}

.imageMobile {
    object-fit: cover;
    max-width: 100%;
}
.portraitMobile {
    max-width: 40%;
    border-radius: 100%;
    margin-top: 1em;
}

.flex {
    display: flex;
}

.flexColumn {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.desktopTitle {
    font-family: 'Lato';
    font-weight: lighter;
    margin-left: 5%;
}

#title {
    font-size: 50px;
}
.desktopNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    max-width: 100%;
}

.mobileNavContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
}

.mobileTitle {
    font-size: 50px;
    font-family: 'Lato';
    font-weight: lighter;
    text-align: center;
}

.mobileNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* SERVICES - DESKTOP - SECTIONS 1 & 2 */

.servicesSectionOne p, .servicesSectionOneMobile p {
    font-size: 80%;
}

.medicareList li, .medicareListMobile li, .moreList li, .moreListMobile li, .infoLinks a, .infoLinksMobile a {
    font-size: 90%;
}
.servicesSectionOne {
    background-color: #ABB7B7;
    height: 60vh;
    width: 60vw;
    padding: 5%;
    line-height: 170%;
    font-size: 110%;
}

.servicesSectionOneFiller {
    background-color: white;
    height: 60vh;
    width: 40vw;
}

.servicesSectionTwo {
    display: flex;
    background-color: #ABB7B7;
    padding: 5%;
}

.medicare {
    background-color: var(--services);
    height: 200%;
    width: 50%;
    margin-right: 1%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    line-height: 230%;
    border-radius: 5%;
}

.more {
    background-color: var(--services);
    height: 200%;
    width: 50%;
    padding: 5%;
    margin-left: 1%;
    display: flex;
    flex-direction: column;
    align-items: left;
    border-left: 3% solid #ABB7B7;
    line-height: 230%;
    border-radius: 5%;
}

.medicareMoreH2 {
    color: #ECF0F1;
    text-align: center;
}

.medicareList {
    text-align: center;
}

.moreList {
    text-align: center;
}

/* SERVICES - MOBILE - SECTIONS 1 & 2 */

.servicesSectionOneMobile {
    background-color: #ABB7B7;
    line-height: 160%;
    font-size: 100%;
    height: 200%;
    max-width: 100%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: right;
}


.servicesSectionTwoMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ABB7B7;
    padding: 2.5%;
}

.medicareMobile {
    background-color: var(--services);
    height: 150%;
    width: 90%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-bottom: 3%;
    line-height: 250%;
    border-radius: 5%;
}

.moreMobile {
    background-color: var(--services);
    height: 150%;
    width: 90%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-bottom: 3%;
    line-height: 250%;
    border-radius: 5%;
}

.medicareMoreH2Mobile {
    text-align: center;
    color: #ECF0F1;
}

.medicareListMobile {
    text-align: center;
}

.moreListMobile {
    text-align: center;
}
/*SERVICES - SECTION 3*/
.servicesSectionThree {
    display: flex;
    justify-content: center;
    background-color: #ECF0F1;
}

.servicesSectionThreeMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ECF0F1;
}

.informationBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 45%;
    background-color: var(--services);
    padding: 7.5%;
    line-height: 250%;
    margin: 5%;
    border-radius: 5%;
}

#infoTitle {
    margin-bottom: 1em;
    text-align: center;
    color: #ECF0F1;
}

.infoLinks {
    display: flex;
    flex-direction: column;
    align-items: left;
    font-size: 120%;
    text-align: center;
}

.infoLinks a {
    color: black;
    text-decoration: underline;
    padding: 10px;
}

.infoLinks a:hover {
    background-color: #ECF0F1;
    color: #2980B9;
    text-decoration: none;
}

.brandImage {
    max-width: 45%;
    height: auto;
    margin: 5%;
    border-radius: 25%;
}

.informationBoxMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    height: auto;
    background-color: var(--services);
    padding: 7.5%;
    line-height: 250%;
    margin: 5%;
    border-radius: 5%;
}

#infoTitleMobile {
    margin-bottom: 1em;
    text-align: center;
    color: #ECF0F1;
}

.brandImageMobile {
    width: 90%;
    height: auto;
    margin: 5%;
    border-radius: 25%;
}

.infoLinksMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #2980B9;
    font-size: 120%;
}

.infoLinksMobile a {
    color: black;
    text-decoration: underline;
    padding: 10px;
}
.infoLinksMobile a:hover {
    background-color: #ECF0F1;
    color: #2980B9;
}